.TripItinerary {
	display: flex;
	flex-direction: column;
	align-items: center;
	position: relative;
	background-color: var(--darkBlue);
	background-image: url('../../../images/Backgrounds/det-trip-itinerary-bg.svg');
	background-size: cover;
}

.TripItineraryBg {
	width: 100%;
}

.TripItineraryContent {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
	width: 90vw;
	overflow: hidden;
	margin: 3vw 5vw;
}

.TripItineraryTitle {
	margin: 0;
	font-weight: 700;
	font-style: normal;
	font-size: var(--webHeading2);
	color: var(--baseWhite);
}

.TripItineraryTitleOrange {
	color: var(--baseOrange);
}

.TripItineraryIntro {
	font-size: var(--webNormalFont);
	color: var(--shade6White);
	padding: 2em 0 3em;
	width: 70%;
}

.TripItineraryText {
	width: 100%;
}

/* navbar */
.ItineraryTabs {
	display: block;
	overflow-x: auto;
	white-space: nowrap;
	width: 90vw;
	border-top-left-radius: 8px;
	border-top-right-radius: 8px;
	font-size: calc(0.2em + 1vw);
	border-bottom: 0;
}

nav.ItineraryTabs::after {
	display: none;
}

div.shadow-wrapper.itinerary-wrapper {
	height: 3.5vw;
	width: 3vw;
	right: 0;
	position: absolute;
	background-image: linear-gradient(
		to right,
		rgba(255, 255, 255, 0),
		var(--baseBlue)
	);
}

/* scrollbar */

nav.ItineraryTabs::-webkit-scrollbar {
	height: 5px;
	width: 0;
}

nav.ItineraryTabs::-webkit-scrollbar-thumb {
	background: var(--baseOrange);
	border-radius: 5px;
}

nav.ItineraryTabs::-webkit-scrollbar-track {
	border: 0;
	background: lightgray;
	border-radius: 5px;
}

/* tabs */
nav.ItineraryTabs.nav.nav-tabs a.nav-item.nav-link {
	display: inline-block;
	color: var(--shade2Blue) !important;
	min-width: 25%;
	border-right: 1px solid var(--shade2Blue);
	padding: 0.6em 0;
	margin: 0;
	text-align: center;
	background-color: var(--shade4Orange);
}
nav.ItineraryTabs.nav.nav-tabs a.nav-item.nav-link.active {
	color: var(--baseWhite) !important;
}
.IternaryTab-content {
	width: 90vw;
	background-color: var(--shade2Blue);
	box-shadow: -2px -4px 4px rgba(0, 0, 0, 0.2);
}

.mobile-tab-content {
	width: 90vw;
}

div.showInMobileScreenOnly nav.nav.nav-tabs {
	margin-top: 5vh;
	border: none;
	overflow-x: auto;
	flex-wrap: nowrap;
	width: 90vw;
}

div.showInMobileScreenOnly nav.nav.nav-tabs a.nav-item.nav-link {
	font-size: 3.4vw;
	min-width: fit-content;
	margin-left: 0;
	margin-right: 3.8vw;
	padding: 0;
}

nav.ItineraryTabs.nav.nav-tabs a.nav-item.nav-link.active {
	background: var(--shade2Blue);
	color: var(--baseWhite);
	border: none;
	text-align: center;
	margin: 0;
}

.ItineraryTripCard {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin: 2em 0 4em;
	transition: ease-in 0.2s;
	cursor: pointer;
}

.ItineraryTripCardContent {
	width: 84%;
	border-radius: 8px;
	margin-bottom: 2em;
}

.ItineraryTripCardHeading {
	font-size: calc(0.2em + 1.2vw);
	color: var(--baseWhite);
	font-weight: 500;
	margin: 1em 0 2em;
}
div.ItineraryTripCardContent div.react-multi-carousel-list,
div.ItineraryTripCardContent div.react-multi-carousel-list ul {
	width: 76vw;
}
.carousel .control-dots .dot {
	background-color: var(--baseWhite);
	opacity: 1;
}

.carousel .control-dots .dot.selected {
	background-color: var(--baseOrange);
}

.TripItinerary .react-multiple-carousel__arrow--right {
	right: 0;
}

.ItineraryTripCard .react-multiple-carousel__arrow--right {
	right: calc(5% + 1px);
}

.TripItinerary .react-multiple-carousel__arrow--left {
	left: 0;
}

.ItineraryTripCard .react-multiple-carousel__arrow--left {
	left: calc(5% + 1px);
}

.mentoor .react-multiple-carousel__arrow--left {
	top: 14em;
	left: 1vw;
}

.mentoor .react-multiple-carousel__arrow--right {
	top: 14em;
	right: 1vw;
}

.mentoor .react-multi-carousel-list {
	align-items: unset;
}

.tab-content > .active {
	color: var(--baseWhite);
}

/* empire-intro */
div.TripItinerary div.empire-intro {
	display: flex;
	justify-content: center;
	width: 90vw;
	height: auto;
	color: white;
}

.empire-intro-content h2 {
	font-size: var(--webHeading2);
	font-weight: 600;
}

.empire-intro-content p {
	font-size: var(--webNormalFont);
	margin-top: 1.5em;
}

.ItineraryPersonalCardHeading {
	font-size: var(--webNormalFont2);
	padding: 4vw 7vw 2vw;
	font-weight: 600;
}

.PersonalTab .ItineraryCard {
	padding: 3em 4.5em 3em;
}

.PersonalTab .mentoor .react-multiple-carousel__arrow--right {
	top: auto;
}

.PersonalTab .mentoor .react-multiple-carousel__arrow--left {
	top: auto;
}

.personalGraphics {
	width: 20vw;
	max-height: 13vw;
}

.PersonalTab .ItineraryEmptyCard {
	max-height: 12em;
}

.PersonalTab {
	padding-bottom: 4vw;
}

.ItineraryTripCard .MentorImageDiv {
	height: 11vw;
}

.TripItenaryButtonFlex {
	display: flex;
	justify-content: space-between;
}