/* section */
.need-section {
    display: flex;
    background-color: var(--baseWhite);
}

.need-section img.need-section-bg {
    width: 100%;
}

main.need-section-content {
    position: absolute;
    margin-top: 4vw;
    width: 100%;
}

/* main */
.need-section-content h2 {
    margin-top: 1.4vw;
    margin-left: 7vw;
    font-family: 'Poppins';
    font-weight: 700;
    font-size: var(--webHeading);
}

/* cards wrapper */
.need-align-cards {
    display: flex;
    justify-content: center;
    height: 100%;
}

/* cards */
.need-cards {
    padding-top: 4vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 84vw;
}

.need-cards div.pseudo-card {
    margin-top: 5vw;
    display: flex;
    align-items: center;
    height: 3vw;
}

.need-cards div.pseudo-card.skewed {
    padding: 1vw;
    margin-bottom: 12vw;
}

.pseudo-card div.card {
    width: 30vw;
    height: 12vw;
    padding: 2vw;
    justify-content: center;
    align-items: center;
    font-size: 1.1vw;
}

.pseudo-card div.card section {
    font-family: 'Poppins';
    margin-bottom: 0;
    color: var(--shade5Blue);
}

.pseudo-card div.skewed-card {
    margin-left: auto;
    height: 16vw;
}

.skewed-card section {
    margin: 0;
}

.pseudo-card.skewed div.skewed-card.card div.need-icon {
    padding: 0.2vw;
}

.card div.need-card-text {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.card div.need-card-text div.need-icon {
    padding: 1vw;
    flex: 0 1 0vw;
}

.need-icon img {
    max-width: 6vw;
}

.need-card-text h4 {
    font-size: 2vw;
    font-weight: 700;
}

.need-card-text section {
    line-height: 1.5vw;
    padding-left: 1vw;
    flex: 1 1 3vw;
}

.need-card-text ul {
    padding-left: 1.3vw;
    margin-bottom: 1vw;
}