.ItineraryCard {
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 8em 4.5em 3em;
	color: var(--baseWhite);
	margin-bottom: 2em;
}

.Card {
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	height: fit-content;
}

.ItineraryEmptyCard {
	background-color: var(--shade3Blue);
	width: 80vw;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 8px;
	box-shadow: 0px 2px 16px #162228;
	max-height: 15em;
}

.ItineraryContentCard {
	background-color: var(--shade3Blue);
	width: 95%;
	border-radius: 8px;
	box-shadow: 0px 2px 16px #162228;
	text-align: left;
	padding: 2.2em 3em 2em 3em;
}

.ItineraryCardHeading {
	font-size: calc(0.2em + 1.2vw);
	color: var(--baseWhite);
	margin-top: 2vw;
	font-weight: 500;
}

.ItineraryMentorDetails {
	display: flex;
	justify-content: space-between;
}

.MentorHighlights {
	padding: 2em 1em 0 0;
	width: 50%;
	margin-right: 1em;
}

.SeparatorLine {
	border-right: 1.8px dashed var(--shade2Orange);
	width: 2px;
	margin-left: 2.5em;
}

.MentorDetails {
	display: flex;
	justify-content: center;
	flex-direction: column;
	width: 12em;
	border-radius: 8px;
	margin: 0 2em;
	color: var(--baseWhite);
}

.HighlightsText {
	font-size: calc(0.2em + 0.9vw);
}

.HighlightsTextOrange {
	color: var(--shade2Orange);
	font-size: calc(0.2em + 0.9vw);
	font-weight: 500;
}

ul.control-dots {
	margin: 0 !important;
}
