.intro-page {
  padding: 10vw 5vw;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: var(--baseBlue);
  color: var(--baseWhite);
  text-align: center;
}

.intr-title {
  margin: 1em 0 1em;
  font-weight: 700;
  font-style: normal;
  font-size: var(--webHeading);
}

.intr-details {
  font-size: var(--webNormalFont2);
  color: var(--shade3White);
  width: 50%;
}

.det-intr {
  margin-bottom: 2em;
}
