.ItinerarySuperCard {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin: 4em 0;
	cursor: pointer;
}

.ItinerarySuperCardContent {
	width: 84%;
	background-color: var(--shade3Blue);
	padding: 1.8em 3em 1.4em;
	border-radius: 8px;
	box-shadow: 0px 2px 16px #162228;
}

.ItinerarySuperCardHeading {
	font-size: calc(0.2em + 1.2vw);
	color: var(--baseWhite);
	font-weight: 500;
}

.SuperMentorDetails {
	margin-top: 1em;
	display: flex;
	justify-content: center;
	flex-direction: column;
	background-color: var(--shade4Blue);
	width: 12em;
	border-radius: 8px;
	margin-right: 2em;
	color: var(--baseWhite);
}

.ItinerarySuperBox {
	display: flex;
	align-items: center;
	justify-content: space-around;
}

.SuperMentorPastExperiences {
	width: 50%;
	color: var(--baseWhite);
	padding-left: 10%;
}

.PastExperiencesTitle {
	font-size: calc(0.2em + 1.1vw);
	color: var(--shade2Orange);
}

ul.PastExperiencePoints {
	list-style: none;
	font-size: calc(0.2em + 1vw);
}

ul.PastExperiencePoints li::before {
	content: '\2022';
	color: var(--baseOrange);
	font-size: calc(0.5em + 1.3vw);
	font-weight: bold;
	display: inline-block;
	width: 1.2em;
	margin-left: -1em;
}
