.MentorName {
	font-size: calc(0.2em + 0.9vw);
	font-weight: 700;
	margin: 1em 0 0 0.5em;
}

.MentorIntro {
	font-size: calc(0.2em + 0.5vw);
	margin: 0.2em 0 0.5em 0.8em;
}

.MentorImage {
	width: 100%;
	height: 100%;
}

.MentorImageDiv {
	position: relative;
	background-color: var(--lightBlue);
	border-top-right-radius: 8px;
	border-top-left-radius: 8px;
	display: flex;
	justify-content: center;
}

.MentorMentor {
	display: flex;
	background-color: var(--shade4Blue);
	justify-content: space-between;
	align-items: flex-end;
}

.MentCompany {
	max-width: 1.5em;
	margin-right: 0.8em;
	margin-bottom: 0.6em;
	border-radius: 50%;
}

.MentorFor {
	height: 1.4em;
	line-height: 1.4em;
	width: 70%;
	background-color: var(--shade4Blue);
	position: absolute;
	top: 95%;
	border-radius: 4px;
	font-size: calc(0.2em + 0.6vw);
	text-align: center;
	color: var(--baseWhite);
	overflow: hidden;
}

div.datacard:hover {
	color: var(--shade2Orange);
	border-radius: 8px;
}
